<template>
  <div class="list">
      <el-table :data="tableData" border>
        <el-table-column  label="头图">
            <template slot-scope="scope">
                <img :src="scope.row.img" alt="">
            </template>
        </el-table-column>
        <el-table-column prop="title" label="工程名称">
        </el-table-column>
        <el-table-column prop="content" label="施工内容">
        </el-table-column>
        <el-table-column prop="adress" label="工程地点">
        </el-table-column>
        <el-table-column label="操作">
            <template slot-scope="scope">
                <el-button
                size="mini"
                @click="edit(scope.row._id)">编辑</el-button>
                <el-button
                size="mini"
                type="danger"
                @click="del(scope.row._id)">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name:'cases_list',
  data() {
      return {
          tableData:[]
      }
  },
  methods:{
      getData() {
          this.$axios.get('getCases').then(res => {
              this.tableData = res.data
          })
      },
      del(id) {
        //   console.log(id)
        this.$confirm('是否永久删除该案例？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$axios.post('delCases',{id}).then(res => {
                if(res.code == 200) {
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                    setTimeout(()=> {
                        this.$router.go(0)
                    },1000)
                }else{
                    this.$message({
                        type: 'danger',
                        message: '删除失败!'
                    });
                }
            })    
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
      edit(id) {
          this.$router.push({path:'/case/update',query:{id}})
      }
  },
  created() {
      this.getData()
  }
}
</script>
<style scoped>
.list{
    padding:20px;
}
img{
    max-height: 80px;
}
</style>



